<template>
    <div class="change-user-pic">
        <mooc-mall-header></mooc-mall-header>
        <mooc-mall-bread>
            EDITUSERPIC
        </mooc-mall-bread>
        <div class="user-info-main">
            <div class="container boxshadowActive">
                <div class="user-info-cover">
                    <div :style="'background-image: url(\'usercover/'+userCover" class="user-info-cover-image"></div>
                    <div class="user-info-cover-imgae-upload" @click.stop="uploaduserCover">
                        <div class="user-info-cover-image-upload-wrapper">
                            <input style="display: none;" @change="handuserCoverFile"
                                   class="user-info-cover-image-upload-file" type="file">
                            <Button class="user-info-cover-image-upload-btn" icon="ios-camera-outline" type="primary">
                                编辑封面图片
                                <Progress v-if="userCoverpercent>0&&userCoverpercent<100" :percent="userCoverpercent"/>
                            </Button>
                        </div>
                    </div>
                </div>
                <div class="user-info-pic-wrapper">
                    <div class="user-info-pic">
                        <img :src="'userpic/'+userPic"/>
                        <div class="user-info-pic-upload"
                             :class="{'activeuserPicpercent': userPicpercent>0&&userPicpercent<100}"
                             @click.stop="uploaduserImg">
                            <div class="user-info-pic-upload-modal">
                                <input accept="image/gif, image/jpeg, image/png, image/jpg" class="user-upload-input"
                                       @change="handleFile"
                                       id="inputUserPic"
                                       type="file">
                                <Icon type="ios-camera-outline" size="30"/>
                                <p>点击更换头像</p>
                                <Progress v-if="userPicpercent>0&&userPicpercent<100" :percent="userPicpercent"/>
                            </div>
                        </div>
                    </div>
                    <div class="user-info-pic-detail">
                        <h1>{{userInfo}}</h1>
                        <div v-show="userGender!=''" class="user-info-pic-detail-gender">
                            <Icon v-show="userGender=='male'" type="md-male"/>
                            <Icon v-show="userGender=='female'" type="md-female"/>
                        </div>
                        <div v-show="userDesc!=''" class="user-info-pic-detail-description">{{userDesc}}</div>
                    </div>
                    <div class="user-info-pic-detail-response">
                        <h1>{{userInfo}}</h1>
                        <div v-show="userGender!=''" class="user-info-pic-detail-gender">
                            <Icon v-show="userGender=='male'" type="md-male"/>
                            <Icon v-show="userGender=='female'" type="md-female"/>
                        </div>
                        <div v-show="userDesc!=''" class="user-info-pic-detail-description">{{userDesc}}</div>
                    </div>
                    <div class="user-info-pic-edit">
                        <el-button @click="personEditVisible=true" size="mini" plain>编辑个人资料</el-button>
                    </div>
                    <Modal
                        v-model="personEditVisible"
                        title="编辑"
                        width="350"
                        class-name="personedit-modal"
                        @on-visible-change="loadPerson"
                        :mask-closable="false"
                        :footer-hide="true">
                        <div class="input-form">
                            <Form ref="formItem" :model="formItem" :label-width="55">
                                <FormItem label="性别">
                                    <RadioGroup v-model="formItem.gender">
                                        <Radio label="male">男</Radio>
                                        <Radio label="female">女</Radio>
                                        <Radio label="secrecy">保密</Radio>
                                    </RadioGroup>
                                </FormItem>
                                <FormItem label="介绍">
                                    <Input v-model="formItem.desc" type="textarea" :autosize="{minRows: 2,maxRows: 5}"
                                           placeholder="介绍一下自己吧..."></Input>
                                </FormItem>
                                <FormItem>
                                    <Button :loading="saveEditLoading" @click="saveEdit" ref="saveEdit" size="large" type="primary">保存</Button>
                                </FormItem>
                            </Form>
                        </div>
                    </Modal>
                </div>
            </div>
            <div class="container">
                <div class="user-info-data">
                    <div class="user-info-data-left">
                        <div class="user-info-data-left-top-menu">
                            <Menu @on-select="changeContent" mode="horizontal" theme="light"
                                  active-name="personCollection">
                                <MenuItem name="personCollection">
                                    <Icon type="md-heart"/>
                                    <span style="vertical-align: middle;">收藏商品<span style="margin-left: 5px;">{{userCollectonProducts.length}}</span></span>
                                </MenuItem>
                            </Menu>
                        </div>
                        <div class="user-info-data-left-bottom-content">
                            <div v-if="activeContent=='personCollection'"
                                 class="user-info-data-left-bottom-personCollection">
                                <ul>
                                    <li v-for="(item, index) in userCollectonProducts" :key=index>
                                        <div class="user-info-data-personCollection-item">
                                            <div class="user-info-data-item-img">
                                                <img :key="item.productImage" v-lazy="'productsImg/'+item.productImage">
                                            </div>
                                            <div class="user-info-data-item-info">
                                                <div class="user-info-data-item-text">{{item.productName}}</div>
                                                <div class="user-info-data-item-price">{{item.salePrice |
                                                    currency('¥')}}
                                                </div>
                                            </div>
                                        </div>
                                        <div @click="delLove(item)" class="user-info-data-personCollection-dellove">
                                            <Tooltip content="取消收藏">
                                                <Icon size="26" type="md-heart"/>
                                            </Tooltip>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                            <div v-if="userCollectonProducts.length==0" class="user-info-data-left-bottom-null">
                                <img src="utils-alibaba/null.png" alt="">
                                <p>暂时没有内容</p>
                            </div>
                        </div>
                    </div>
                    <div class="user-info-data-right"></div>
                </div>
            </div>
        </div>
        <mooc-mall-footer></mooc-mall-footer>
        <input accept="image/gif, image/jpeg, image/png, image/jpg" type="file" id="testFile" style="opacity: 0">
    </div>
</template>

<script>
    function FileListItem (a) {
        a = [].slice.call(Array.isArray(a) ? a : arguments)
        for (var c, b = c = a.length, d = !0; b-- && d;) d = a[b] instanceof File
        if (!d) throw new TypeError('expected argument to FileList is File or array of File objects')
        for (b = (new ClipboardEvent('')).clipboardData || new DataTransfer; c--;) b.items.add(a[c])
        return b.files
    }

    import axios from 'axios'
    import { VueCropper } from 'vue-cropper'
    import MoocMallHeader from '@/components/MoocMallHeader'
    import MoocMallBread from '@/components/MoocMallBread'
    import MoocMallFooter from '@/components/MoocMallFooter'
    import { mallMixin } from '../utils/mixin'

    export default {
        name: 'changeUserPic',
        mixins: [mallMixin],
        components: {
            MoocMallHeader,
            MoocMallBread,
            VueCropper,
            MoocMallFooter
        },
        data () {
            return {
                userCoverpercent: 0,
                userPicpercent: 0,
                activeContent: 'personCollection',
                testImage: '',
                personEditVisible: false,
                formItem: {
                    gender: '',
                    desc: ''
                },
                saveEditLoading: false
            }
        },
        computed: {},
        watch: {
            formItem: {
                handler(newVal) {
                    if (newVal.gender!=this.userGender || newVal.desc!=this.userDesc) {
                        this.$refs.saveEdit.$el.disabled = false
                    }else {
                        this.$refs.saveEdit.$el.disabled = true
                    }
                },
                deep: true
            }
        },
        methods: {
            saveEdit() {
                this.saveEditLoading=true
                axios.post('/users/savePersonInfo',{
                    userGender: this.formItem.gender,
                    userDesc: this.formItem.desc
                }).then(response=>{
                    let res = response.data
                    if (res.status=='0'){
                        this.setUserDesc(res.result.userDesc)
                        this.setUserGender(res.result.userGender)
                        this.personEditVisible=false
                        this.saveEditLoading=false
                        this.$Message.success('保存成功')
                    }
                })
            },
            closePersonModal(){
              this.personEditVisible=false
            },
            loadPerson (flag) {
                if (flag) {
                    this.formItem.gender = this.userGender
                    this.formItem.desc = this.userDesc
                    this.$refs.saveEdit.$el.disabled = true
                }else{
                    // this.personEditVisible=true
                    if (this.formItem.gender!=this.userGender || this.formItem.desc!=this.userDesc){
                        // this.personEditVisible=true
                        this.$Modal.confirm({
                            title: '警告',
                            content: '页面还未保存,是否保存?',
                            okText: '保存退出',
                            cancelText:'直接退出',
                            onOk: this.saveEdit,
                            onCancel: this.closePersonModal
                        })
                    }else{
                        this.personEditVisible=false
                    }
                }
            },
            delLove (product) {
                axios.post('/users/editUserLove', {
                    flag: 'remove',
                    productId: product._id
                }).then(response => {
                    let res = response.data
                    if (res.status == '0') {
                        this.setUserCollectonProducts(res.result)
                        // console.log(res.result)
                        this.$message({
                            showClose: true,
                            message: '操作成功',
                            type: 'success'
                        })
                    }
                })
            },
            // 改变导航菜单对应的内容
            changeContent (name) {
                this.activeContent = name
            },
            uploaduserImg () {
                this.$el.querySelector('.user-upload-input').click()
            },
            uploaduserCover () {
                this.$el.querySelector('.user-info-cover-image-upload-file').click()
            },
            handleFile (e) {
                // alert(123)
                let type = e.target.files[0].type
                let size = e.target.files[0].size
                if (type !== 'image/gif' && type !== 'image/jpeg' && type !== 'image/png' && type !== 'image/jpg') {
                    this.$Notice.warning({
                        title: '文件格式不正确',
                        desc: false ? '' : `您上传的${name}文件格式不正确，请上传image/gif, image/jpeg, image/png, image/jpg格式的图片。`
                    })
                    return
                } else if (size > 2097152) {
                    this.$Notice.warning({
                        title: '文件大小超出',
                        desc: false ? '' : `您上传的${name}文件大小不正确，请保持在2M以内。`
                    })
                    return
                }
                const file = e.target.files[0]
                const fileName = file.name
                let reader = new FileReader()
                reader.readAsDataURL(e.target.files[0])
                reader.onload = function (e) {
                    let result = e.target.result
                    let image = new Image()
                    image.src = result
                    image.onload = function () {
                        let canvas = document.createElement('canvas')
                        let context = canvas.getContext('2d')
                        canvas.width = 200
                        canvas.height = 200
                        let w = image.width
                        let h = image.height
                        let dw = 200 / w
                        let dh = 200 / h
                        if (w > 200 && h > 200 || w < 200 && h < 200) {
                            if (dw > dh) {
                                context.drawImage(image, 0, (h - 200 / dw) / 2, w, 200 / dw, 0, 0, 200, 200)
                            } else {
                                context.drawImage(image, (w - 200 / dh) / 2, 0, 200 / dh, h, 0, 0, 200, 200)
                            }
                        } else {
                            if (w < 200) {
                                context.drawImage(image, 0, (h - 200 / dw) / 2, w, 200 / dw, 0, 0, 200, 200)
                            } else {
                                context.drawImage(image, (w - 200 / dh) / 2, 0, 200 / dh, h, 0, 0, 200, 200)
                            }
                        }
                        // context.drawImage(image, 0, 0, 200, 300) // 在canvas上绘制图片
                        // let dataUrl = canvas.toDataURL('image/jpeg', 0.92) // 0.92为压缩比，可根据需要设置，设置过小会影响图片质量
                        context.canvas.toBlob((blob) => {
                            const newFile = new File([blob], fileName, file)
                            const fileList = new FileListItem(newFile)
                            // console.log(newFile)
                            document.getElementById('testFile').files = fileList
                            // console.log(document.getElementById('testFile').files)
                            // newInputFile.files = fileList
                            // console.log('ol')
                        })
                        // dataUrl 为压缩后的图片资源，可将其上传到服务器
                        // console.log(this)
                        /* let img = document.getElementById('testImageOne')
                         img.src = dataUrl*/
                        // flag = dataUrl

                    }
                }
                setTimeout(() => {
                    let $target = document.getElementById('testFile').files
                    // console.log($target)
                    let data = new FormData()
                    data.append('avatar', $target[0])
                    let config = {
                        onUploadProgress: progressEvent => {
                            // let complete = (progressEvent.loaded / progressEvent.total * 100 | 0)
                            if (progressEvent.lengthComputable) {
                                let complete = Math.round((progressEvent.loaded / progressEvent.total) * 100)
                                this.userPicpercent = complete
                            }
                        }
                    }
                    axios.post('/users/uploadPic', data, config, {
                        headers: { 'content-type': 'multipart/form-data' }
                    }).then(response => {
                        let res = response.data
                        if (res.status == '0') {
                            this.setUserPic(res.result.userPic)
                            this.$message({
                                showClose: true,
                                message: '上传成功',
                                type: 'success'
                            })
                        }
                    })
                }, 500)
            },
            handuserCoverFile (e) {
                // console.log(e)
                let $target = e.target || e.srcElement
                // console.log(e)
                let type = $target.files[0].type
                // console.log(type)
                let name = $target.files[0].name
                let size = $target.files[0].size
                // console.log(size)
                if (type !== 'image/gif' && type !== 'image/jpeg' && type !== 'image/png' && type !== 'image/jpg') {
                    this.$Notice.warning({
                        title: '文件格式不正确',
                        desc: false ? '' : `您上传的${name}图片格式不正确，请上传image/gif, image/jpeg, image/png, image/jpg格式的图片。`
                    })
                    return
                } else if (size > 10485760) {
                    this.$Notice.warning({
                        title: '文件大小超出',
                        desc: false ? '' : `您上传的${name}图片大小不正确，请保持在10M以内。`
                    })
                    return
                }
                let data = new FormData()
                data.append('usercover', $target.files[0])
                let config = {
                    onUploadProgress: progressEvent => {
                        // let complete = (progressEvent.loaded / progressEvent.total * 100 | 0)
                        if (progressEvent.lengthComputable) {
                            let complete = Math.round((progressEvent.loaded / progressEvent.total) * 100)
                            this.userCoverpercent = complete
                        }
                    }
                }
                axios.post('/users/uploadUserCover', data, config, {
                    headers: { 'content-type': 'multipart/form-data' }
                }).then(response => {
                    let res = response.data
                    if (res.status == '0') {
                        this.setUserCover(res.result.userCover)
                        this.$message({
                            showClose: true,
                            message: '上传成功',
                            type: 'success'
                        })
                    }
                })
            }
        }
    }
</script>

<style lang="scss" scoped>

    .change-user-pic {
        .user-info-main {
            margin-top: 20px;

            .boxshadowActive {
                /*box-shadow: 0 0 10px #999;*/
                -webkit-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .1);
                -moz-box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .1);
                box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .1);
                -webkit-border-radius: 8px;
                -moz-border-radius: 8px;
                border-radius: 8px;
            }

            .user-info-cover {
                width: 100%;
                height: 300px;
                position: relative;

                .user-info-cover-image {
                    width: inherit;
                    height: inherit;
                    background-color: #fff;
                    border: 1px solid #e9e9e9;
                    border-top-left-radius: 8px;
                    border-top-right-radius: 8px;
                    /*background-image: url("../../server/public/usercover/usercover.jpg");*/
                    -webkit-background-size: cover;
                    background-size: cover;
                    background-position: 50%;
                    background-repeat: no-repeat;
                }

                .user-info-cover-imgae-upload {
                    position: absolute;
                    top: 10px;
                    right: 10px;

                    .user-info-cover-image-upload-wrapper {
                        .user-info-cover-image-upload-btn {
                            color: hsla(0, 0%, 100%, .7);
                            border-color: hsla(0, 0%, 100%, .24);
                            background-color: transparent;

                            .ivu-progress {
                                width: auto;
                            }

                            &:hover {
                                color: hsla(0, 0%, 100%, .7);
                                background-color: hsla(0, 0%, 100%, .04);
                            }
                        }
                    }
                }
            }

            .user-info-pic-wrapper {
                min-height: 120px;
                border: 1px solid #e9e9e9;
                border-bottom-left-radius: 8px;
                border-bottom-right-radius: 8px;
                background-color: #fff;
                position: relative;
                &::after {
                    clear: both;
                    content: '';
                    display: block;
                    width: 0;
                    height: 0;
                    visibility: hidden;
                }
                .user-info-pic {
                    width: 150px;
                    height: 150px;
                    /*border: 4px solid #e9e9e9;*/
                    -webkit-border-radius: 8px;
                    -moz-border-radius: 8px;
                    border-radius: 8px;
                    position: absolute;
                    left: 20px;
                    top: -50px;
                    overflow: hidden;
                    padding: 4px;
                    background-color: #e9e9e9;
                    cursor: pointer;
                    img {
                        width: 100%;
                        height: 100%;
                        -webkit-border-radius: 4px;
                        -moz-border-radius: 4px;
                        border-radius: 4px;
                        /*display: block;*/
                    }

                    .user-info-pic-upload {
                        opacity: 0;
                        width: 142px;
                        height: 142px;
                        -webkit-border-radius: 4px;
                        -moz-border-radius: 4px;
                        border-radius: 4px;
                        position: absolute;
                        left: 4px;
                        top: 4px;
                        z-index: 20;
                        background-color: rgba(0, 0, 0, .5);
                        text-align: center;
                        color: #ffffff;
                        transition: opacity .4s ease-in-out;

                        .user-info-pic-upload-modal {
                            position: relative;
                            top: 40px;

                            .user-upload-input {
                                display: none;
                            }

                            .ivu-progress {
                                width: auto;
                            }
                        }

                    }

                    .activeuserPicpercent {
                        opacity: 1;
                    }

                    &:hover {
                        .user-info-pic-upload {
                            opacity: 1;
                        }
                    }

                }

                .user-info-pic-detail {
                    margin-left: 190px;
                    line-height: 32px;

                    h1 {
                        font-weight: 700;
                        padding-top: 6px;
                        font-size: 20px;
                    }

                    .user-info-pic-detail-description {
                        text-align: justify;
                        margin-right: 160px;
                        margin-bottom: 20px;
                        font-size: 0.8rem;
                    }
                }
                .user-info-pic-detail-response {
                    display: none;
                }

                .user-info-pic-edit {
                    position: absolute;
                    right: 10px;
                    bottom: 10px;
                }

            }

            .user-info-data {
                margin-top: 16px;
                /*height: 240px;*/
                /*border: 1px solid #000;*/
                display: flex;
                flex-wrap: nowrap;
                justify-content: space-between;

                .user-info-data-left {
                    width: 70.83333333%;

                    .user-info-data-left-top-menu {
                        /*width: 100%;*/
                        .ivu-menu-light {
                            -webkit-border-radius: 8px;
                            -moz-border-radius: 8px;
                            border-radius: 8px;
                        }
                    }

                    .user-info-data-left-bottom-content {
                        border-top: 1px solid #e9e9e9;
                        background-color: #fff;
                        box-shadow: 0 1px 1px 0 rgba(0, 0, 0, .1);
                        -webkit-border-radius: 0 0 8px 8px;
                        -moz-border-radius: 0 0 8px 8px;
                        border-radius: 0 0 8px 8px;

                        .user-info-data-left-bottom-personCollection {
                            padding: 16px;

                            ul {
                                li {
                                    /*width: 100%;*/
                                    display: flex;
                                    padding-right: 15px;
                                    flex-wrap: nowrap;
                                    justify-content: space-between;
                                    align-items: center;
                                    border: 2px solid #e9e9e9;
                                    margin-top: 20px;
                                    -webkit-border-radius: 8px;
                                    -moz-border-radius: 8px;
                                    border-radius: 8px;
                                    /*background-color: #e9e9e9;*/
                                    .user-info-data-personCollection-item {
                                        /*height: inherit;*/
                                        .user-info-data-item-img {
                                            /*float: left;*/
                                            width: 110px;
                                            display: inline-block;
                                            vertical-align: middle;
                                            border-right: 2px solid #e9e9e9;
                                            padding: 2px;
                                            overflow: hidden;

                                            img {
                                                width: 100%;
                                                height: 100%;
                                                display: inline-block;
                                            }
                                        }

                                        .user-info-data-item-info {
                                            /*float: left;*/
                                            display: inline-block;
                                            vertical-align: middle;
                                            line-height: 2.3;
                                            margin-left: 15px;

                                            .user-info-data-item-text {
                                                color: #000;
                                                font-weight: 700;
                                                font-size: 18px;
                                            }

                                            .user-info-data-item-price {
                                                color: #D1434A;
                                                font-weight: 700;
                                                font-size: 18px;
                                            }
                                        }

                                    }

                                    .user-info-data-personCollection-dellove {
                                        cursor: pointer;
                                        transition: color .3s ease-in-out;
                                    }
                                }
                            }
                        }

                        .user-info-data-left-bottom-null {
                            text-align: center;
                            /*padding: 8px 8px 0 0;*/
                            padding-bottom: 25px;

                            img {
                                width: 235px;
                            }

                            p {
                                color: #999;
                            }
                        }
                    }
                }

                .user-info-data-right {

                }
            }
        }
    }
</style>
